import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

const apiDomain:string = 'https://westeurope.api.cognitive.microsoft.com/vision/v1.0/analyze?visualFeatures=Categories,Description,Tags&details'

@Injectable()
export class VisionApiService {

  constructor(private http:Http) { }

  parseImage(url){
    const data = {
      url:url
    };
    let headers = new Headers();
    headers.append('ocp-apim-subscription-key', '2b5d145a03f240d6a96000b51c7108b1');
    headers.append('Content-Type', 'application/json');
    return this.http.post(apiDomain, data, {headers: headers})
    .map( res => res.json());
  }

}



