import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import {VisionApiService} from '../../services/vision-api.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  inputImageURL:string = null;
  imageSubmited:boolean = false;
  categories = null;
  captions = null;
  tags = null;

  constructor(
    private flashMessagesService:FlashMessagesService,
    private visionApiService:VisionApiService
  ) { }

  ngOnInit() {
  }

  onUploadImageClick(){
    this.visionApiService.parseImage(this.inputImageURL).subscribe(
      data => {
        console.log("OK DATA");
        console.log(data);
        this.imageSubmited = true;
        this.categories = data.categories;
        this.captions = data.description.captions;
        this.tags = data.tags;

      },
      err => {
        console.log("NOT OK ERR");
        console.log(err);
        return false;
      });

  }


}



